*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

main {
  /* ie fix */
  display: block;
  width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6,
button {
  margin: 0;
  padding: 0;
}

ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

input,
button,
textarea,
select {
  font: inherit;
}

button {
  display: block;
  background: none;
  border: none;
  color: inherit;
  user-select: none;
}

button:hover {
  cursor: pointer;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* COMMON */
.header {
  height: 67px;
  display: flex;
  justify-content: space-between;
  background-image: linear-gradient(to bottom, #e9e9e9, #fff);
}

.header__logo {
  max-width: 150px;
}

.header__menu {
  margin-right: auto;
}

.main-menu {
  padding: 0 18px;
}

.main-menu__list {
  display: flex;
  align-items: center;
}

.main-menu__link {
  height: 67px;
  display: flex;
  align-items: center;
  padding: 0 18px;
  transition: all .3s;
}

.main-menu__link:hover {
  background-color: #b3ffe5;
}

body {
  background: #fff;
  color: #000;
  font-family: "Nunito", "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5em;
}
